import React from 'react'
import { useEffect } from 'react';
import { Form, Input, Button, Checkbox, Select } from 'antd';
import { useState } from 'react'
import { useFormik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import { Tabs } from 'antd';
import _ from 'lodash';
import { quanLyNguoiDungService } from '../../services/QuanLyNguoiDung';
import { GROUPID, USER_LOGIN } from '../../util/settings/config';
import { capNhatThongTinCaNhanAction, layThongTinNguoiDungAction, layThongTinTaiKhoanAction } from '../../redux/actions/QuanLyNguoiDungAction';
import moment from 'moment/moment';
import * as Yup from 'yup';

const Profile = (props) => {

    const [state, setState] = useState({
        loaiNguoiDung: [],
    });
    const dispatch = useDispatch();

    const thongTinCaNhan = useSelector(state => state.QuanLyNguoiDungReducer.taiKhoan);

    const [passwordShown, setPasswordShown] = useState(false);
    const togglePassword = () => {
        // When the handler is invoked
        // inverse the boolean state of passwordShown
        setPasswordShown(!passwordShown);
    };
    const convertSelectLND = () => {
        return state.loaiNguoiDung?.map((lnd, index) => {
            return { label: lnd.maLoaiNguoiDung, value: lnd.maLoaiNguoiDung }
        })
    }
    const handleCMaLoaiNguoiDung = (value) => {
        formik.setFieldValue('maLoaiNguoiDung', value)
    }

    useEffect(async () => {
        let userLogin = JSON.parse(localStorage.getItem(USER_LOGIN));
        dispatch(layThongTinTaiKhoanAction(userLogin.taiKhoan));
        try {
            let result = await quanLyNguoiDungService.layDanhSachLoaiNguoiDung();
            setState({
                ...state,
                loaiNguoiDung: result.data.content
            })
        } catch (error) {
        }
    }, [])

    const validationSchema = Yup.object({
        matKhau: Yup.string()
            .required('Vui lng in mt khu.')
            .min(6, 'Mt khu phi c t nht 6 k t.'),
        email: Yup.string()
            .email('Email khng hp l.')
            .required('Vui lng nhp email.'),
        soDt: Yup.string()
            .required('Vui lng nhp s in thoi.')
            .matches(/^[0-9]+$/, 'S in thoi ch c cha cc s.')
            .min(9, 'S in thoi phi c t nht 9 s.')
            .max(13, 'S in thoi ch c 13 k t.'),
        hoTen: Yup.string()
            .required('Vui lng nhp h tn.')
            .matches(/^[a-zA-Z\u00C0-\u024F\s]+$/, 'H tn ch c cha k t.'),
    });

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: {
            taiKhoan: thongTinCaNhan[0]?.taiKhoan,
            matKhau: thongTinCaNhan[0]?.matKhau,
            email: thongTinCaNhan[0]?.email,
            soDt: thongTinCaNhan[0]?.soDt,
            maNhom: GROUPID,
            maLoaiNguoiDung: thongTinCaNhan[0]?.maLoaiNguoiDung,
            hoTen: thongTinCaNhan[0]?.hoTen
        },
        validationSchema,
        onSubmit: (values) => {

            dispatch(capNhatThongTinCaNhanAction(values))
        }
    })
    return (
        <div className='container-fluid'>
            <h3 className="text-2xl">Thng tin ti khon</h3>
            <Form
                name="basic"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
                onSubmitCapture={formik.handleSubmit}
            >
                <Form.Item label="Ti khon">
                    <Input name='taiKhoan' value={formik.values.taiKhoan} disabled />
                </Form.Item>
                <Form.Item label="Mt khu">
                    <Input name='matKhau' type={passwordShown ? "text" : "password"} onChange={formik.handleChange} value={formik.values.matKhau} placeholder='Nhp mt khu' />
                    {formik.touched.matKhau && formik.errors.matKhau ? (
                        <div className='text-red-600'>{formik.errors.matKhau}</div>
                    ) : null}
                    <Button onClick={() => {
                        togglePassword()
                    }}>Show Password</Button>
                </Form.Item>
                <Form.Item label="Email">
                    <Input name='email' type='email' onChange={formik.handleChange} value={formik.values.email} placeholder='Nhp Email lin h' />
                    {formik.touched.email && formik.errors.email ? (
                        <div className='text-red-600'>{formik.errors.email}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="S in thoi">
                    <Input
                        name="soDt"
                        type="text" // Thay i kiu thnh 'text'
                        onChange={formik.handleChange}
                        value={formik.values.soDt}
                        placeholder="Nhp s in thoi lin h"
                    />
                    {formik.touched.soDt && formik.errors.soDt ? (
                        <div className='text-red-600'>{formik.errors.soDt}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="H tn ngi dng">
                    <Input name='hoTen' onChange={formik.handleChange} value={formik.values.hoTen} placeholder='Nhp h tn ngi dng' />
                    {formik.touched.hoTen && formik.errors.hoTen ? (
                        <div className='text-red-600'>{formik.errors.hoTen}</div>
                    ) : null}
                </Form.Item>
                <Form.Item style={{display:'none'}} label="M loi ngi dng">
                    <Select disabled options={convertSelectLND()} onChange={handleCMaLoaiNguoiDung} value={formik.values.maLoaiNguoiDung} placeholder="Chn loi ngi dng" />
                </Form.Item>
                <Form.Item label="Tc v">
                    <button type="submit" className="bg-blue-300 text-white p-2">Cp nht ti khon</button>
                </Form.Item>
            </Form>
        </div>
    )
}

const { TabPane } = Tabs;

export default function ProfileTabs(props) {
    const { tabActive } = useSelector(state => state.QuanLyDatVeReducer);
    const dispatch = useDispatch();

    useEffect(() => {
        return () => {
            dispatch({
                type: 'CHANGE_TAB_ACTIVE',
                number: '1'
            })
        }
    }, [])

    return <div className="p-5">
        <Tabs style={{ marginTop: '100px' }} defaultActiveKey="1" activeKey={tabActive} onChange={(key) => {

            // console.log('key',  key)
            dispatch({
                type: 'CHANGE_TAB_ACTIVE',
                number: key.toString()
            })
        }}>
            <TabPane tab="01 THNG TIN TI KHON" key="1" >
                <Profile {...props} />
            </TabPane>
            <TabPane tab="02 KT QU T V" key="2">
                <KetQuaDatVe {...props} />
            </TabPane>
        </Tabs>

    </div>
}

function KetQuaDatVe(props) {

    const dispatch = useDispatch();
    const { thongTinNguoiDung } = useSelector(state => state.QuanLyNguoiDungReducer);

    const { userLogin } = useSelector(state => state.QuanLyNguoiDungReducer);


    useEffect(() => {
        const action = layThongTinNguoiDungAction();
        dispatch(action)
    }, [])

    // console.log('thongTinNguoiDung', thongTinNguoiDung);

    const renderTicketItem = function () {
        return thongTinNguoiDung.thongTinDatVe?.map((ticket, index) => {
            const seats = _.first(ticket.danhSachGhe);

            return <div className="p-2 lg:w-1/3 md:w-1/2 w-full" key={index}>
                <div className="h-full flex items-center border-gray-200 border p-4 rounded-lg">
                    <img alt="team" className="w-16 h-16 bg-gray-100 object-cover object-center flex-shrink-0 rounded-full mr-4" src={ticket.hinhAnh} />
                    <div className="flex-grow">
                        <h2 className="text-pink-500 title-font font-medium text-2xl">{ticket.tenPhim}</h2>
                        <p className="text-gray-500"><span className="font-bold">Gi chiu:</span> {moment(ticket.ngayDat).format('hh:mm A')} - <span className="font-bold">Ngy chiu:</span>  {moment(ticket.ngayDat).format('DD-MM-YYYY')} .</p>
                        <p><span className="font-bold">a im:</span> {seats.tenHeThongRap}   </p>
                        <p>
                            <span className="font-bold">Tn rp:</span>  {seats.tenCumRap} - <span className="font-bold">Gh:</span>  {ticket.danhSachGhe.map((ghe, index) => { return <span className="text-green-500 text-xl" key={index}> [ {ghe.tenGhe} ] </span> })}
                        </p>
                    </div>
                </div>
            </div>
        })
    }

    return <div className="p-5">

        <section className="text-gray-600 body-font">
            <div className="container px-5 py-24 mx-auto">
                <div className="flex flex-col text-center w-full mb-20">
                    <h1 className="sm:text-3xl text-2xl font-medium title-font mb-4  text-purple-600 ">Lch s t v khch hng</h1>
                    <p className="lg:w-2/3 mx-auto leading-relaxed text-base">Hy xem thng tin a v thi gian  xem phim vui v bn nh !</p>
                </div>
                <div className="flex flex-wrap -m-2">
                    {renderTicketItem()}

                </div>
            </div>
        </section>

    </div>
}

