import React from 'react'
import { useFormik } from 'formik';
import { useDispatch } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { dangNhapAction } from '../../redux/actions/QuanLyNguoiDungAction';
import * as Yup from 'yup'
import './Login.css'


export default function Login(props) {

    const dispatch = useDispatch();

    const validationSchema = Yup.object({
        taiKhoan: Yup.string()
            .required('Vui lng nhp ti khon.'),
        matKhau: Yup.string()
            .required('Vui lng nhp mt khu.')
            .min(6, 'Mt khu phi c t nht 6 k t.'),
    });

    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
        },
        validationSchema,
        onSubmit: values => {

            const action = dangNhapAction(values);
            dispatch(action);

            // console.log('values', values);
        },
    });

    return (
        <div className='Login'>
            <div className='Login_textbox'>
                <div className='container'>
                    <img src='https://www.cgv.vn/skin/frontend/cgv/default/images/cgvlogo.png' alt='logo'/>
                    <p>Vui lng ng nhp  nhn nhiu u i</p>
                </div>
                <div>
                    <form onSubmit={formik.handleSubmit}>
                        <div className='mt-3'>
                            <div className="text-lg font-bold text-white tracking-wide">Ti khon</div>
                            <input name="taiKhoan" onChange={formik.handleChange} onBlur={formik.handleBlur}  className="w-full text-lg py-2 border-b border-gray-300 focus:outline-none" value={formik.values.taiKhoan} placeholder="Nhp vo ti khon" />
                            {formik.touched.taiKhoan && formik.errors.taiKhoan ? (
                                <div className='text-red-600'>{formik.errors.taiKhoan}</div>
                            ) : null}
                        </div>

                        <div className='mt-3'>
                            <div className="text-lg font-bold  text-white tracking-wide">Mt khu</div>
                            <input type="password" name="matKhau" onChange={formik.handleChange} onBlur={formik.handleBlur} className="w-full text-lg py-2 border-b border-gray-300 focus:outline-none" value={formik.values.matKhau} placeholder="Nhp vo mt khu" />
                            {formik.touched.matKhau && formik.errors.matKhau ? (
                                <div className='text-red-600'>{formik.errors.matKhau}</div>
                            ) : null}
                        </div>

                        <div className="mt-10">
                            <button type="submit" style={{background:'#e50813'}} className=" text-gray-100 p-4 w-full rounded-full tracking-wide font-semibol text-base">
                                ng nhp
                            </button>
                        </div>
                        <div className="mt-6 text-sm font-display font-semibold text-white text-center">
                            Bn cha c ti khon ? <NavLink to="/register" className="cursor-pointer text-indigo-600 hover:text-indigo-800">ng k</NavLink>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
