import { useFormik } from 'formik';
import React from 'react'
import { useDispatch } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { dangKyAction } from '../../redux/actions/QuanLyNguoiDungAction';
import { GROUPID } from '../../util/settings/config';
import * as Yup from 'yup';
import './Register.css'

export default function Register() {

    const dispatch = useDispatch();

    const validationSchema = Yup.object({
        taiKhoan: Yup.string()
            .required('Vui lng in ti khon.')
            .matches(/^[a-zA-Z0-9]+$/, 'Ti khon ch c cha k t v s.')
            .min(6, 'Ti khon phi c t nht 6 k t.')
            .max(12, 'Ti khon ch c 12 k t.'),
        matKhau: Yup.string()
            .required('Vui lng in mt khu.')
            .min(6, 'Mt khu phi c t nht 6 k t.'),
        email: Yup.string()
            .email('Email khng hp l.')
            .required('Vui lng nhp email.'),
        soDt: Yup.string()
            .required('Vui lng nhp s in thoi.')
            .matches(/^[0-9]+$/, 'S in thoi ch c cha cc s.')
            .min(9, 'S in thoi phi c t nht 9 s.')
            .max(13, 'S in thoi ch c 13 k t.'),
        hoTen: Yup.string()
            .required('Vui lng nhp h tn.')
            .matches(/^[a-zA-Z\u00C0-\u024F\s]+$/, 'H tn ch c cha k t.'),
    });

    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            maNhom: GROUPID,
            hoTen: ''
        },
        validationSchema,
        onSubmit: values => {

            const action = dangKyAction(values);
            dispatch(action);
        },
    });


    return (
        <div className='Register'>
            <div className='Register_textbox'>
                <div className='container'>
                    <img src='https://www.cgv.vn/skin/frontend/cgv/default/images/cgvlogo.png' alt='logo' />
                    <p>ng k thnh vin  nhn nhiu u i</p>
                </div>
                <div>
                    <form onSubmit={formik.handleSubmit}>
                        <div className="mt-1">
                            <div className="text-base font-bold text-white tracking-wide">Ti khon</div>
                            <input name="taiKhoan" onChange={formik.handleChange} className="w-full text-base py-2 border-b border-gray-300 focus:outline-none focus:border-indigo-500" placeholder="Nhp vo ti khon" />
                            {formik.touched.taiKhoan && formik.errors.taiKhoan ? (
                                <div className='text-red-600'>{formik.errors.taiKhoan}</div>
                            ) : null}
                        </div>

                        <div className="mt-1">
                            <div className="text-base font-bold text-white tracking-wide">Email</div>
                            <input name="email" onChange={formik.handleChange} className="w-full text-base py-2 border-b border-gray-300 focus:outline-none focus:border-indigo-500" placeholder="Nhp vo email" />
                            {formik.touched.email && formik.errors.email ? (
                                <div className='text-red-600'>{formik.errors.email}</div>
                            ) : null}
                        </div>

                        <div className="mt-1">
                            <div className="text-base font-bold text-white tracking-wide">S in thoi</div>
                            <input name="soDt"
                                type="text"
                                onChange={formik.handleChange}
                                value={formik.values.soDt}
                                placeholder="Nhp s in thoi lin h" className="w-full text-base py-2 border-b border-gray-300 focus:outline-none focus:border-indigo-500" />
                            {formik.touched.soDt && formik.errors.soDt ? (
                                <div className='text-red-600'>{formik.errors.soDt}</div>
                            ) : null}
                        </div>

                        <div className="mt-1">
                            <div className="text-base font-bold text-white tracking-wide">H tn</div>
                            <input name="hoTen" onChange={formik.handleChange} className="w-full text-base py-2 border-b border-gray-300 focus:outline-none focus:border-indigo-500" placeholder="Nhp vo h tn" />
                            {formik.touched.hoTen && formik.errors.hoTen ? (
                                <div className='text-red-600'>{formik.errors.hoTen}</div>
                            ) : null}
                        </div>

                        <div className="mt-1">
                            <div className="text-base font-bold text-white tracking-wide">Mt khu</div>
                            <input type="password" name="matKhau" onChange={formik.handleChange} onBlur={formik.handleBlur} className="w-full text-base py-2 border-b border-gray-300 focus:outline-none" value={formik.values.matKhau} placeholder="Nhp vo mt khu" />
                            {formik.touched.matKhau && formik.errors.matKhau ? (
                                <div className='text-red-600'>{formik.errors.matKhau}</div>
                            ) : null}
                        </div>

                        <div className="mt-3">
                            <button type="submit" style={{ background: '#e50813' }} className=" text-gray-100 p-4 w-full rounded-full tracking-wide font-semibol text-base">
                                ng K
                            </button>
                        </div>
                        <div className="mt-3 text-sm font-display font-semibold text-white text-center">
                            Bn  c ti khon ? <NavLink to="/login" className="cursor-pointer text-indigo-600 hover:text-indigo-800">ng nhp ngay</NavLink>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
