import React from 'react'
import { useEffect } from 'react';
import { Form, Input, Select } from 'antd';
import { useState } from 'react'
import { quanLyNguoiDungService } from '../../../../services/QuanLyNguoiDung';
import { useFormik } from 'formik';
import { GROUPID } from '../../../../util/settings/config';
import * as Yup from 'yup';

const AddUser = (props) => {

    const [state, setState] = useState({
        loaiNguoiDung: [],
    })
    console.log('loaiNguoiDung', state.loaiNguoiDung)
    useEffect(async () => {
        try {
            let result = await quanLyNguoiDungService.layDanhSachLoaiNguoiDung();
            setState({
                ...state,
                loaiNguoiDung: result.data.content
            })
        } catch (error) {
        }
    }, [])

    const convertSelectLND = () => {
        return state.loaiNguoiDung?.map((lnd, index) => {
            return { label: lnd.maLoaiNguoiDung, value: lnd.maLoaiNguoiDung }
        })
    }
    const handleCMaLoaiNguoiDung = (value) => {
        formik.setFieldValue('maLoaiNguoiDung', value)
    }

    const validationSchema = Yup.object({
        taiKhoan: Yup.string()
            .required('Vui lng in ti khon.')
            .matches(/^[a-zA-Z0-9]+$/, 'Ti khon ch c cha k t v s.')
            .min(6, 'Ti khon phi c t nht 6 k t.')
            .max(12, 'Ti khon ch c 12 k t.'),
        matKhau: Yup.string()
            .required('Vui lng in mt khu.')
            .min(6, 'Mt khu phi c t nht 6 k t.'),
        email: Yup.string()
            .email('Email khng hp l.')
            .required('Vui lng nhp email.'),
        soDt: Yup.string()
            .required('Vui lng nhp s in thoi.')
            .matches(/^[0-9]+$/, 'S in thoi ch c cha cc s.')
            .min(9, 'S in thoi phi c t nht 9 s.')
            .max(13, 'S in thoi ch c 13 k t.'),
        hoTen: Yup.string()
            .required('Vui lng nhp h tn.')
            .matches(/^[a-zA-Z\u00C0-\u024F\s]+$/, 'H tn ch c cha k t.'),
    });

    const formik = useFormik({
        initialValues: {
            taiKhoan: '',
            matKhau: '',
            email: '',
            soDt: '',
            maNhom: GROUPID,
            maLoaiNguoiDung: '',
            hoTen: ''
        },
        validationSchema,
        onSubmit: async (values) => {
            console.log('values', values);
            try {
                const result = await quanLyNguoiDungService.themNguoiDung(values);

                alert('Thm ngi dng thnh cng');

            } catch (error) {
                console.log('error', error.response?.data)
            }
        }
    })
    return (
        <div className='container-fluid'>
            <h3 className="text-2xl">Thm ngi dng</h3>
            <Form
                name="basic"
                labelCol={{ span: 6 }}
                wrapperCol={{ span: 14 }}
                onSubmitCapture={formik.handleSubmit}
            >
                <Form.Item label="Ti khon">
                    <Input name='taiKhoan' onChange={formik.handleChange} placeholder='Nhp ti khon ngi dng' />
                    {formik.touched.taiKhoan && formik.errors.taiKhoan ? (
                        <div className='text-red-600'>{formik.errors.taiKhoan}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="Mt khu">
                    <Input name='matKhau' type='password' onChange={formik.handleChange} placeholder='Nhp mt khu' />
                    {formik.touched.matKhau && formik.errors.matKhau ? (
                        <div className='text-red-600'>{formik.errors.matKhau}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="Email">
                    <Input name='email' type='email' onChange={formik.handleChange} placeholder='Nhp Email lin h' />
                    {formik.touched.email && formik.errors.email ? (
                        <div className='text-red-600'>{formik.errors.email}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="S in thoi">
                    <Input name='soDt' type='number' onChange={formik.handleChange} placeholder='Nhp s in thoi lin h' />
                    {formik.touched.soDt && formik.errors.soDt ? (
                        <div className='text-red-600'>{formik.errors.soDt}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="H tn ngi dng">
                    <Input name='hoTen' onChange={formik.handleChange} placeholder='Nhp h tn ngi dng' />
                    {formik.touched.hoTen && formik.errors.hoTen ? (
                        <div className='text-red-600'>{formik.errors.hoTen}</div>
                    ) : null}
                </Form.Item>
                <Form.Item label="M loi ngi dng">
                    <Select options={convertSelectLND()} onChange={handleCMaLoaiNguoiDung} placeholder="Chn loi ngi dng" />
                </Form.Item>
                <Form.Item label="Tc v">
                    <button type="submit" className="bg-blue-300 text-white p-2">Thm ngi dng</button>
                </Form.Item>
            </Form>
        </div>
    )
}

export default AddUser;